
import { validateTaxesWithAvalara } from './services/LambdaService';
interface TaxDetail {
    taxName: string;
    taxCalculated: number;
}

export const calculateTaxes = async (cart: any, shippingAddress: any, lineItems: any, customerId: any, shippingCost:any) => {
    const date = new Date().toISOString().split('T')[0];
    const allItems = [
        ...lineItems.physicalItems,
        ...lineItems.digitalItems,
        ...lineItems.customItems,
        ...lineItems.giftCertificates,
    ];

    // Crear las líneas de productos
    const itemsWithShipping = allItems.map((item: any, index: number) => ({
        number: `${index + 1}`, // Número de línea único
        quantity: item.quantity || 1, // Cantidad de producto, por defecto 1
        amount: item.listPrice || item.amount || 0, // Precio del producto
        taxCode: 'P0000000', // Código de impuestos genérico para productos
        itemCode: item.sku || item.itemCode || `item-${index + 1}`, // SKU o código del artículo
        description: item.name || 'Product', // Descripción del artículo
    }));
    if (shippingCost > 0) {
        itemsWithShipping.push({
            number: `${itemsWithShipping.length + 1}`, // Número de línea único
            quantity: 1,
            amount: shippingCost, // Costo de envío
            taxCode: 'FR00000', // Código de impuestos específico para el envío
            itemCode: 'shipping', // Código para identificar el costo de envío
            description: 'Shipping Cost', // Descripción del envío
        });
    }

    // Construir el JSON que se enviará a Avalara
    const taxRequest = {
        id: cart.id, // Usamos el cart.id como el código
        code: cart.id, // Usamos el cart.id como el código de la transacción
        adjustmentReason: 'Other', // Ajuste genérico, puede cambiarse según sea necesario
        adjustmentDescription: 'Transaction for tax calculation',
        createTransactionModel: {
            lines: itemsWithShipping.map((item: any, index: number) => ({
                number: `${index + 1}`, // Número de línea único
                quantity: item.quantity,
                amount: item.amount, // Precio del artículo o costo de envío
                taxCode: item.taxCode, // Código de impuestos para el artículo o envío
                itemCode: item.itemCode, // Código del producto o envío
                description: item.description, // Descripción del producto o envío
            })),
            type: 'SalesInvoice', // Tipo de transacción
            date: date, // Fecha de la compra en formato YYYY-MM-DD
            customerCode: customerId, // Usamos el customerId como customerCode
            purchaseOrderNo: `${date}-${cart.id}`, // Número de la orden de compra
            addresses: {
                singleLocation: {
                    line1: shippingAddress.address1, // Dirección de envío
                    line2: shippingAddress.address2,
                    city: shippingAddress.city,
                    region: shippingAddress.region, // Estado o región
                    country: shippingAddress.country,
                    postalCode: shippingAddress.postalCode,
                },
            },
            commit: false, // No se comitea la transacción, solo se calcula
            currencyCode: 'USD', // Moneda
            description: 'Order transaction', // Descripción general de la transacción
        },
    };

    try {
        const response = await validateTaxesWithAvalara(taxRequest);
        const { totalTax, summary } = response;
        if (totalTax > 0) {
            const taxes = summary
                .filter((tax: TaxDetail) => tax.taxCalculated > 0)
                .map((tax: TaxDetail) => ({
                    name: tax.taxName,
                    amount: tax.taxCalculated,
                }));
            return taxes;

        } else {
            return  [];
        }
    } catch (error) {
        console.error('Error al calcular los impuestos:', error);
        throw error;
    }
};